.image-info {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.image-info article {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: #00f;
  color: #ffff00;
  opacity: 0;
  -webkit-transition: 0.15s linear opacity;
  -o-transition: 0.15s linear opacity;
  transition: 0.15s linear opacity;
  display: -ms-flexbox;
  display: flex;
  padding: 2rem;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
  line-height: 2rem;
}

.image-info hr {
  width: 45px;
  border: 1px solid #ffff00;
}

.image-info.hovered article {
  opacity: 1;
}

.image-info p:first-child {
  font-size: 1.6rem;
  text-transform: uppercase;
}

.image-info p:last-child {
  font-size: 1.2rem;
  text-transform: lowercase;
}

@media only screen and (max-width: 1200px) {
  .image-info p:first-child {
    font-size: 1.5rem;
  }
}



